@font-face {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: normal;
	src: url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.woff2) format('woff2'),
		url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.woff) format('woff'),
		url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Regular.otf) format('opentype');
}
@font-face {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: bold;
	src: url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.woff2) format('woff2'),
		url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.woff) format('woff'),
		url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.otf) format('opentype');
}

/* Default */
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,acronym,address,big,cite,code,del,dfn,em,font,important,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,button{border:0;background:transparent;margin:0;padding:0;font-style:normal;font-family:/*'Spoqa Han Sans',*/'Lato','Noto Sans KR','Noto Sans Korean','Noto Sans Korean','nanum gothic','Sans Serif';color:inherit;outline:0;}
a {border:0;background:transparent;margin:0;padding:0;font-style:normal;font-family:/*'Spoqa Han Sans',*/'Lato','Noto Sans KR','Noto Sans Korean','Noto Sans Korean','nanum gothic','Sans Serif';outline:0;}
html {font-size: 16px;height: 100%;}
body{line-height:1;height: 100%;}
ol,ul{list-style:none}
li{list-style:none;}
section, article, aside, hgroup, header, footer, nav, figure, figcaption, summary {display:block;}
address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:normal;}
table {border-collapse:collapse; border-spacing:0;}
textarea {resize:none;}
em{font-style:normal;}
a {text-decoration: none}
a:hover{text-decoration:none;}
label {cursor:pointer; }
input,textarea{margin:0; vertical-align:bottom;overflow:visible;font-family: inherit;}
select {-webkit-appearance:none;}
html {overflow-y: auto;-webkit-text-size-adjust:none;}
button {cursor:pointer;font-size:inherit;border-radius: 0;}
img {border:0;}
.blind {display: none!important;}
.disno {display: none!important;}

/*html,*/
/*body {height: 100%;}*/

h1,h2,h3,h4,h5,h6 {line-height: 1.6;}

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak { display: none !important; }

.cf:before,
.cf:after {
	content: " ";
	display: table;
}

.cf:after {
	clear: both;
}

pre {font-family: monospace;border:1px solid #ccc;background: #f8f8f8;padding:10px;line-height: 1.3;text-align: left;}

